	display: inline-flex;
	margin: 50px;
}



nav p {
	font-size: 20px;
	list-style: none;
	margin: 0 20px;
	color: #b2b1b1;
	cursor: pointer;
	text-decoration: none;
	border: 3px solid transparent;
	border-radius: 3px;
	background-image: linear-gradient(#000000, #000000), radial-gradient(circle at top left, #fd00da, #19d7f8);
	background-origin: border-box;
	background-clip: content-box, border-box;
	
}
nav a{
	display: block;
	padding: 15px 15px;
}


